body, #root {
    width: 350px;
    height: 600px;
}
.scrollbar {
    overflow-x: hidden;
    height: 494px;
}

::-webkit-scrollbar {
    background-color: #424242;
}

::-webkit-scrollbar-thumb {
    background-color: #202020;
}

::-webkit-scrollbar-thumb:hover {
    background-color: #606060;
}